import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import TitleBar from 'components/topbar'
import connect from 'connect'
import { Button, WhiteSpace, WingBlank, Steps, List, Grid, Card } from 'antd-mobile';
const data1 = Array.from(new Array(4)).map(() => ({
  icon: require('../../images/tj11.png'),
  icon1: require('../../images/tj11.png'),
}));


@connect

class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
           disabled: false,
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }


    render () {
      const data = [
        {
          img: require('../../images/lian.png'),
          title: 'Meet hotel',
          des: '运送中',
          value: 0,
        },
      ];
      const Step = Steps.Step;

      const customIcon = () => (
       
        <svg id="ellipsis" viewBox="0 0 44 44" width="100%" height="100%" className="am-icon am-icon-md">
          <circle cx="21.888" cy="22" r="15" stroke="#bbb" strokeWidth="2" fill="#ffffff"></circle>
        </svg>
      );

      const activeIcon = () => (
       
        <svg id="ellipsis" viewBox="0 0 44 44" width="100%" height="100%" className="am-icon am-icon-md">
          <circle cx="21.888" cy="22" r="15" stroke="#3DCFBB" strokeWidth="2" fill="#ffffff"></circle>
        </svg>
      );
        const Item = List.Item;
        const Brief = Item.Brief;
        return (
            <div className="express-container">
            <section className="title-bar-wrapper" style={{backgroundColor: '#D3C8B6'}}>
                <div className="head">
                    <img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>
                    <h2>物流详情</h2>
                </div>
            </section>
            {data.map((obj, index) => (
             <div  key={'myordercard' + index} >
              <Card full={true}>
                    <Card.Header
                      thumb={require('../../images/lian.png')}
                      thumbStyle={{width: '1rem',height: '0.75rem', borderRadius: '0.03rem'}}
                      extra={
                       <div style={{ }}>
                         <div className="destext">状态：运送中</div>
                         <div className="destext">快递编号：11223335466789544</div>
                         <div className="destext">快递公司：圆通速递</div>
                         <div className="destext">客服电话：0371-12345678</div>
                       </div>
                      }
                    />
              </Card>
           </div>
            ))}
            <div style={{height: '0.6rem'}}></div>
            <div className="detailpane">
              <div className='expresstitle'>物流动态</div>
                <Steps current={0}>
                      <Step title="包裹已到达漯河市，正在派件中" icon={activeIcon()} description="2018-04-20 13:55" />
                      <Step title="已到达郑州转运中心，下一站发往漯河市转运中心" icon={customIcon()} description="2018-04-20 13:55" />
                      <Step title="包裹已发出，下一站郑州市转运中心" icon={customIcon()} description="2018-04-20 13:55" />
                      <Step title="圆通快递公司已揽收" icon={customIcon()} description="2018-04-20 13:55" />
                      <Step title="卖家已发货" icon={customIcon()} description="2018-04-20 13:55" />
                    </Steps>
            </div>
     </div>
        )
    }
}
export default Home


